<template>
    <div>
        <Titulo texto="Hola"/>
        <h2>Parametro: {{ $route.params.id }}</h2>
        <div class="container">
            <div class="card" style="width: 18rem;">
                <img src="../assets/llama.png" class="card-img-top" alt="...">
                <div class="card-body">
                    <h5 class="card-title">Titulo</h5>
                    <p class="card-text">{{articulo.title}}</p>
                </div>
            <span>Descripcion</span>
            <ul class="list-group list-group-flush">
                <li class="list-group-item">{{articulo.body}}</li>
            </ul>
                <div class="card-body">
                    <a href="/Blog" class="card-link btn btn-success">Atras</a> | 
                    <router-link to="/Blog" class="btn btn-danger" >Atras con router link</router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Titulo from "../components/Titulo.vue"

export default {
    components:{
        Titulo
    },
    data() {
        return {
            articulo: {}
        }
    },
    methods:{
        async consumirArticulo(){
            try {
                const data = await fetch(`https://jsonplaceholder.typicode.com/posts/${this.$route.params.id}`)
                const art = await data.json()
                this.articulo = art
            } catch (error) {
                console.log(error)
            }
        }
    },
    created(){
        this.consumirArticulo()
    }
}
</script>

